import{
    HOTLINEASSIGN_SUCCESS,
    HOTLINEASSIGN_FAIL
} from "../actions/types";

const deafaultState={};
deafaultState.assignhotlinedata=[];

export default function(state=deafaultState,action){
    const {type,payload}=action;
    
    switch(type){
        case HOTLINEASSIGN_SUCCESS:
            return{
                ...state,
                assignhotlinedata:payload.assignhotlinedata
            };
        case HOTLINEASSIGN_FAIL:
            return{
                ...state,
                assignhotlinedata:[],
            }
        default:
            return state;
    }
}

