import React, { useEffect, useState } from "react";
import {
  Form,
  Select,
  Input,
  Button,
  Upload,
  Row,
  Col,
  DatePicker,
  Alert,
} from "antd";
import AddIcoModal from "../../components/modal/AddIcoModal";
import { connect } from "react-redux";
import ReactHtmlParser from 'react-html-parser';
import moment from 'moment';
import authHeader from '../../services/auth-header';
import axios from "axios";

import "../form/form.scss";
const { TextArea } = Input;
const { Option } = Select;

const CreateHotlineStepOne = ({ next,getStepOneValue }) => {
  const [hotlineForm, setHotlineForm] = useState({ directoryName: "", description: "", status: "1", sDate: "", tDate: "",hotlineIconId:"", iconHtml:"", iconText:"",hotline_name:"", hotlineError:"", })
  const[iconId,setIconId]=useState('');
  const[iconHtml,setIconHtml]=useState('');
  const hotline_name =hotlineForm.directoryName;
  const[hotlineErrorUpdated,sethotlineErrorUpdated]=useState('');
  
  

if(hotlineForm.hotlineIconId == 1)
{
  hotlineForm.iconText ="Sexual Harassment"
}
if(hotlineForm.hotlineIconId == 2)
{
  hotlineForm.iconText ="Voilence"
}
if(hotlineForm.hotlineIconId == 3)
{
  hotlineForm.iconText ="Racism"
}
if(hotlineForm.hotlineIconId == 4)
{
  hotlineForm.iconText ="Cyber Bullying"
}
if(hotlineForm.hotlineIconId == 5)
{
  hotlineForm.iconText ="Fight"
}
if(hotlineForm.hotlineIconId == 6)
{
  hotlineForm.iconText ="Discrimination"
}
if(hotlineForm.hotlineIconId == 7)
{
  hotlineForm.iconText ="Slap"
}
  const stepOneNext = (e) => {
    e.preventDefault();
    getStepOneValue(hotlineForm);
    next();
  }
 
  const addIcon = (values, id) =>{
    setIconHtml(values);
    setIconId(id);
    setHotlineForm({...hotlineForm, hotlineIconId:id, iconHtml:values})
}
  const handleChange = (e) => {
    

    setHotlineForm({ ...hotlineForm, [e.target.id]: e.target.value })
  }
  const handleOnSelect = (value, e, name) => {
    setHotlineForm({ ...hotlineForm, [name]: value })
  }
  const handleDatePickerChange = (date, dateString, name) => {
    setHotlineForm({ ...hotlineForm, [name]: date })
  }

  const disabledDate = (current) => {
    return current && current <= moment().endOf('day');
  }
  const token = authHeader();

    const options = {
      headers: {
        'Authorization': token,
        'Accept': 'application/json, text/plain',
        'Content-Type': 'application/json;charset=UTF-8'
      }
    };
  const CheckHotlineName = (e) => {
    
  setHotlineForm({ ...hotlineForm, [e.target.id]: e.target.value })
  
    axios.post('http://10.0.6.10:8000/api/V1/checkhotline/',{hotline_name: hotlineForm.directoryName}, options)
    .then(res => {
      if(res.data){
     // console.log((JSON.stringify(res.data[0])));
      
      hotlineForm.hotlineError = res.data[0];
      //console.log(hotlineForm.hotlineError);
      sethotlineErrorUpdated(hotlineForm.hotlineError);
      
      }
      
    }) 
   } 
  const { RangePicker } = DatePicker;
  return (
    <Form className="input-md w-75" name="validate_other"  >
      <Form.Item
        name="directoryName"
      rules={[
        {
          required: true,
          message: "Please enter directory name",
          
        },
      ]}
      >
        <TextArea
          showCount
          maxLength={80}
          value={hotlineForm.directoryName}
          onChange={e => handleChange(e)}
          onBlur={ e => CheckHotlineName(e) }
          id={'directoryName'}
          className="input-control textarea-input"
          placeholder="Directory Name"
          htmlFor="directoryName"
        />
        {hotlineErrorUpdated =="status: true" &&
        <Alert message="Sorry! This hotline already exists in the system. Please use a different name." type="error"  showIcon></Alert>
        }
        {hotlineErrorUpdated =="status: false" &&
        <Alert message="Hotline name is valid. Please fill the description" type="success" showIcon></Alert>
        }
        
      </Form.Item>
      <Form.Item
        name="description"
      rules={[
        {
          required: true,
          message: "Please enter description",
        },
      ]}
      >

{hotlineForm.description.length<15 &&
        <Alert message="Please enter more the 15 characters in the description below" type="info"  showIcon></Alert>
        }
        <TextArea
          className="input-control textarea-md"
          value={hotlineForm.description}
          onChange={e => handleChange(e)}
          id={'description'}
          placeholder="Description"
          htmlFor="description"
        />
      </Form.Item>
      
      <Form.Item className="input-control datepicker-wrapper">
        <DatePicker
        placeholder = "Select Start Date"
          className="datepicker w-50 mr-5"
          name="sdate"
          value={hotlineForm.sdate}
          onChange={(date, dateString) => handleDatePickerChange(date, dateString, 'sDate')}
          disabledDate={disabledDate}
          rules={[
            {
              required: true,
              message: "Please select start date",
            },
          ]}
        />
        <DatePicker className="datepicker w-50"
        placeholder = "Select End Date"
          name="tDate"
          value={hotlineForm.tDate}
          onChange={(date, dateString) => handleDatePickerChange(date, dateString, 'tDate')}
          disabledDate={disabledDate}
        />
      </Form.Item>
      <Form.Item
      
        name="status"
      rules={[
        {
          
          required: true,
          message: "Please select status",
        },
      ]}
      >
        <Select
        placeholder ="Status"
          className="input-control w-50"
          value={hotlineForm.status}
          htmlFor="status"
          onSelect={(value, event) => handleOnSelect(value, event, 'status')}
        >
          <Select.Option value="1">Active</Select.Option>
          <Select.Option value="0">Inactive</Select.Option>
        </Select>
      </Form.Item>
      <Form.Item className="">
        <Form.Item
          className="upload-control"
          name="upload"
          valuePropName="fileList"
        >
          <AddIcoModal addIcon={addIcon} />
          {(hotlineForm.iconText).length>0 && 

          <div className="ant-upload-list ant-upload-list-picture">
            <div className="ant-upload-list-item ant-upload-list-item-list-type-picture">
              <div className="ant-upload-list-item-info d-inline-flex w-100">
                <span className="d-inline-flex align-items-center">
                  <a
                    className="ant-upload-list-item-thumbnail" 
                  >
                    {ReactHtmlParser(iconHtml)}
                  </a>

                  <span
                    className="ant-upload-list-item-name ant-upload-list-item-name-icon-count-1"
                    title={hotlineForm.iconText}
                  >
                    {hotlineForm.iconText}
                  </span>
                  {/* <span className="uploaded-icon icon icon-close d-inline-flex justify-content-end align-items-center"></span> */}
                </span>
              </div>
            </div>
          </div>
}
        </Form.Item>

      </Form.Item>
      <Form.Item>
        <Button type="primary" htmlType="submit" onClick={stepOneNext}>
          Next
            </Button>
      </Form.Item>
    </Form>
  );

}
export default CreateHotlineStepOne;